import React from 'react'
import './trainers.scss'
import Shivanshu from '../../../Images/shivanshu.png'
import Anash from '../../../Images/anash.png'
import Pramod from '../../../Images/pramod.jpg'


export default function Trainers() {
  return (
    <div className="trainers">
        <h1>Our Trainers</h1>
        <div className="trainer1">
            <div className="left">
                <div className="img_cont">
                    <img src={Shivanshu}/>
                </div>
                
            </div>
            <div className="right">
                <h3>Mr. Shivanshu Kakkar</h3>
                <p>I care deeply about my clients, and there’s nothing of more value to me than helping somebody go through an experience that makes them happy, confident, and strong. I realize how being overweight affects many aspects of your life, and I want to be there for you and help you discover the benefits and joys of training that helped me become the person I am today. I’m here to be your personal guide on every step of the journey.</p>
            </div>
        </div>
        <div className="trainer2">
            
            <div className="left">
                <h3>Mr. Anash</h3>
                <p>I’m very passionate about helping others find happiness in life and feel good about themselves. I believe that when you FEEL GOOD, you excel in life. For me, a healthy lifestyle is much more than just lifting weights. It’s about stretching, warming up, eating healthily, and getting necessary rest and enough sleep. I love helping others see and experience what their bodies are capable of with just enough hard work. I make it a goal to help you achieve a happy and well-balanced lifestyle that will minimize the risk of future health-related issues.</p>
            </div>
            <div className="right">
                <div className="img_cont">
                    <img src={Anash}/>
                </div>
            </div>
        </div>
        <div className="trainer1">
            <div className="left">
                <div className="img_cont">
                    <img src={Pramod}/>
                </div>
            </div>
            <div className="right">
                <h3>Mr. Pramod</h3>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Omnis repudiandae consectetur ipsa eum temporibus et? Architecto nemo aliquid rerum. Iure eos similique aperiam perspiciatis fuga, nulla harum reprehenderit! Voluptate ex eligendi mollitia doloribus at blanditiis necessitatibus repellendus animi ea voluptates facere expedita ipsam, tempore placeat hic sit nemo, quis ad?</p>
            </div>
        </div>
    </div>
  )
}
