import React from 'react'
import './features.scss'
import Expert from '../../../../Images/expert.png'
import Location from '../../../../Images/location.png'
import Equipment from '../../../../Images/equipment.png'
import Price from '../../../../Images/price.png'

export default function Features() {
  return (
    <div className='feature_body'>
        <h1>Why consider us!</h1>
        <div className="features">
            <div className="feature">
                <div className="image">
                    <img src={Expert} />
                </div>
                <div className="text">
                    <h4>Expert advice</h4>
                </div>
            </div>
            <div className="feature">
                <div className="image">
                    <img src={Equipment} />
                </div>
                <div className="text">
                    <h4>Quality equipments</h4>
                </div>
            </div>
            <div className="feature">
                <div className="image">
                    <img src={Price} />
                </div>
                <div className="text">
                    <h4>Affordable prices</h4>
                </div>
            </div>
            <div className="feature">
                <div className="image">
                    <img src={Location} />
                </div>
                <div className="text">
                    <h4>Great location</h4>
                </div>
            </div>
        </div>
    </div>
  )
}
