import { useMemo, React } from 'react';
import { GoogleMap, Marker } from "@react-google-maps/api";
import './map.scss'


function Map() {
  const center = useMemo(()=>({lat: 30.56366,lng: 77.29013}),[]);

  return (
    <GoogleMap zoom={15} center={center} mapContainerClassName="mapbox" >
      <Marker position={center}/>
    </GoogleMap>
  )
}

export default Map