import React from 'react'
import './about.scss'
import Trainers from './trainers/Trainers'
import Gallery from './gallery/Gallery'
import Logo from '../../Images/Logo2.png'

export default function about() {
  return (
    <div className="about">
      <div className="info">
        <div className="left">
          <img src={Logo}/>
        </div>
        <div className="right">
          <h3>Townfit training club is one of the best and premium gym in nahan and it is powered by being strong equipments which are best in class and we provide defined and periodised workout plans for all types of individual and we cater both men and women. Our gym has the best training environment and we will get better with your support and love. We request all of you to come and visit us.</h3>
        </div>
      </div>
      <Trainers/>
      <Gallery/>
    </div>
  )
}
