import React from 'react'
import './perks.scss'
import scale_img from '../../../../Images/scale.png'
import health_img from '../../../../Images/health.png'
import strength_img from '../../../../Images/strength.png'

export default function Perks() {
  return (
    <div className="perks">
        <div className="perk">
            <div className="upper">
                <div className="left">
                    <img src={scale_img} alt="Weighing Scale" />
                </div>
                <div className="right">
                    <h3>Helps regulate weight</h3>
                </div>
            </div>
            <div className="lower">
                <h4>Exercise can help prevent excess weight gain or help maintain weight loss. When you engage in physical activity, you burn calories. The more intense the activity, the more calories you burn.</h4>
                <p>Regular trips to the gym are great, but don't worry if you can't find a large chunk of time to exercise every day. Any amount of activity is better than none at all. To reap the benefits of exercise, just get more active throughout your day — take the stairs instead of the elevator or rev up your household chores. Consistency is key.</p>
            </div>
        </div>
        <div className="perk">
            <div className="upper">
                <div className="left">
                    <img src={health_img} alt="" />
                </div>
                <div className="right">
                    <h3>Reduces health risks</h3>
                </div>
            </div>
            <div className="lower">
                <h4>Regular exercise helps prevent or manage many health problems and concerns. It can also help improve cognitive function and helps lower the risk of diseases.</h4>
                <p>Worried about heart disease? Hoping to prevent high blood pressure? No matter what your current weight is, being active boosts high-density lipoprotein (HDL) cholesterol, the "good" cholesterol, and it decreases unhealthy triglycerides. This one-two punch keeps your blood flowing smoothly, which decreases your risk of cardiovascular diseases.</p>
            </div>
        </div>
        <div className="perk">
            <div className="upper">
                <div className="left">
                    <img src={strength_img} alt="Strength Image" />
                </div>
                <div className="right">
                    <h3>Strengthen your body</h3>
                </div>
            </div>
            <div className="lower">
                <h4>Exercise plays a vital role in building and maintaining strong muscles and bones. Activities like weightlifting can stimulate muscle building when paired with adequate protein intake. </h4>
                <p>Practicing regular physical activity is essential to reducing muscle loss and maintaining strength as you age. Exercise also helps build bone density when you’re younger, it can also slow the loss of bone density that comes with age in addition to helping prevent osteoporosis later in life.</p>
            </div>
        </div>
    </div>
  )
}
