import React from 'react'
import './topbar.scss'

export default function Topbar({menuState, setMenuState}) {
  return (
    <div className={"top-bar " + (menuState && "active")}>
      <div className="left">
        <h2>.the_town_fit</h2>
      </div>
      <div className="right">
        <div className="inner" onClick={()=> setMenuState(!menuState)}>
          <span id='top-line'/>
          <span id='middle-line'/>
          <span id='bottom-line'/>
        </div>
      </div>
    </div>
  )
}
