import React, { useState } from 'react'
import './gallery.scss'
import GYM1 from '../../../Images/GYM1.JPG'
import GYM2 from '../../../Images/GYM2.JPG'
import GYM3 from '../../../Images/GYM3.JPG'
import GYM4 from '../../../Images/GYM4.JPG'
import GYM5 from '../../../Images/GYM5.JPG'
import GYM6 from '../../../Images/GYM6.JPG'
import GYM7 from '../../../Images/GYM7.JPG'
import GYM8 from '../../../Images/GYM8.JPG'
import GYM9 from '../../../Images/GYM9.JPG'

export default function Gallery() {

  const [currentPhoto, setCurrentPhoto] = useState(1);

  const photoData = [{name:GYM1},{name:GYM2},{name:GYM3},{name:GYM4},{name:GYM5},{name:GYM6},{name:GYM7},{name:GYM8},{name:GYM9}];

  const handleClick = (way)=>{
    way === "left" ? setCurrentPhoto(currentPhoto>0 ? currentPhoto-1 : 8): setCurrentPhoto(currentPhoto<(photoData.length-1) ? currentPhoto+1 : 0);
  };

  return (
    <div className="gallery">
      <h1>Sneak Peak into <span>.the_town_fit</span></h1>
      <div className="pictures">
        <div className="left_arrow" onClick={()=>handleClick("left")}>
          <h1>&lt;</h1>
        </div>
        <div className="back_left">
          <img src={photoData[(currentPhoto==0 ? photoData.length-1 : currentPhoto-1)].name}/>
        </div>
        <div className="front">
          <img src={photoData[currentPhoto].name}/>
        </div>
        <div className="back_right">
          <img src={photoData[(currentPhoto==8 ? 0 : currentPhoto+1)].name}/>
        </div>
        <div className="right_arrow" onClick={()=>handleClick("right")}>
          <h1>&gt;</h1>
        </div>
      </div>  
    </div>
  )
}