import React from 'react'
import './top.scss'
import background_img from '../../../Images/background.jpg'
import { Typewriter } from 'react-simple-typewriter'
import InstagramIcon from '@material-ui/icons/Instagram';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import EmailIcon from '@material-ui/icons/Email';

export default function Top() {
  return (
    <div className="top">
      <div className="top_image">
        <img src={background_img} alt="Background" />
      </div>
      <div className="free_div">
        <div className="left">
          <h1>Addicted to <br /> Getting <br /> <span><Typewriter words={['Stronger.', 'Healthier.', 'Bigger.']} loop={0} cursor cursorStyle='_' typeSpeed={90} deleteSpeed={50} delaySpeed={1000} /></span></h1>
        </div>
        <div className="right">
          <div className="icons">
            <InstagramIcon className='icon' onClick={()=> window.open("https://www.instagram.com/the_townfit/", "_self")}/> 
            <WhatsAppIcon className='icon' onClick={()=> window.open("https://wa.me/919805969277", "_self")}/>
            <EmailIcon className='icon' onClick={()=> window.open("mailto:the_town_fit@gmail.com", "_self")}/>
          </div>
        </div>
      </div>
    </div>
  )
}
