import React from 'react'
import './sideMenu.scss'

export default function SideMenu({menuState, setMenuState}) {
  return (
    <div className={"sideMenu " + (menuState && "active")}>
        <ul>
            <li>
                <a href="/.the_town_fit/" onClick={()=> setMenuState(false)}>Home</a>
            </li>
            <li>
                <a href="/.the_town_fit/about" onClick={()=> setMenuState(false)}>About ".the_town_fit"</a>
            </li>
            <li>
                <a href="/.the_town_fit/plans" onClick={()=> setMenuState(false)}>Plans and Pricing</a>
            </li>
            <li>
                <a href="/.the_town_fit/contact" onClick={()=> setMenuState(false)}>Contact us</a>
            </li>
        </ul>
    </div>
  )
}
